import { Selection } from '@ali/4ever-cangjie';

// 只有一种情况 isByTable 会是 -1，所以不用枚举类型。以简化逻辑
export default function setSelectionByTable(
  selection: Selection,
  isByTable: boolean | -1,
  tableType?: 'row' | 'col',
) {
  return selection.merge({
    data: {
      ...selection.data,
      isByTable,
      tableType,
    },
  });
}
